body {
  font-family: 'Montserrat';
  background-color: white;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.particlesjs {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.header-section {
  width: 100%;
  max-width: 500px;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.header-section__content {
  text-align: center;
  margin-top: auto;
}

.header-section__image {
  width: 100%;
  max-width: 100px;
}

.header-section__title {
  margin-bottom: 0;
  font-weight: 700;
  font-size: 3em;
  text-transform: uppercase;
}

.header-section__subtitle {
  margin-top: 0;
  font-weight: 500;
  font-size: 1em;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0.7);
}

.header-section__main-link {
  font-weight: 400;
  text-decoration: none;
  color: black;
  padding: 20px 30px;
  border: 1px solid transparent;
  transition: all 0.2s linear;
  margin: 20px 0;
  z-index: 1;
}

.header-section__main-link:hover {
  border: 1px solid black;
  box-shadow: 4px 4px 0 black;
}

.header-section__footer {
  z-index: 1;
  margin-top: auto;
}

.header-section__footer-link {
  color: black;
  font-weight: 400;
  text-decoration: none;
  padding: 5px 10px;
  border: 1px solid transparent;
  transition: all 0.2s linear;
}

.header-section__footer-link:hover {
  border: 1px solid black;
  box-shadow: 4px 4px 0 black;
}

@media (max-width: 330px) {
  .header-section .header-section__image {
    max-width: 80px;
  }
  .header-section .header-section__title {
    font-size: 2.3em;
  }
  .header-section .header-section__subtitle {
    font-size: 0.8em;
  }
}
